.container{
  max-width: 1274px;
  width: 100%;
  margin: 0 auto;
  @include respond-to("xs","sm","md"){
    padding: 0 20px;
  }
  @media(min-width: 1080px) and (max-width: 1273px){
    padding: 0 20px;
  }
}
html{
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}